.uap-token {
	$token: &;
	
	$padding-left-right: 8px;

	display: inline-flex;
	align-items: center;

	background: #eee;

	color: var(--uap-font-color);

	line-height: 1;

	padding: 3px $padding-left-right;

	border-radius: 150px;

	position: relative;

	box-shadow: 0 0 0 2px transparent;
	transition: box-shadow 150ms ease-in-out;

	cursor: pointer;

	ua-builder-condition-pill & {
		border: 1px solid var(--uap-border-color-gray);
	}

	img {
		width: 12px;
		height: 11px;

		margin-right: 5px;

		object-fit: contain;
		object-position: center;

		display: block;
	}

	uo-icon {
		width: 14px;
		height: 14px;

		border: 1px solid #444;
		border-radius: 50%;

		font-size: 9px;
		color: #444;
		text-align: center;
		line-height: 15px;
		background: #fff;

		opacity: 0.8;

		margin-right: 5px;

		display: inline-flex;
		align-items: center;
		justify-content: center;
	}

	&--loading {
		padding-left: 12px + 5px + $padding-left-right;

		img,
		uo-icon {
			display: none;
		}

		&::before {
			@include icons-font;
			content: var(--uap-icon-loading);

			animation: rotating 2s linear infinite;
			font-size: 12px;
			position: absolute;
			left: $padding-left-right;
		}
	}

	&__id {
		font-size: var(--uap-font-size--body-xsmall);
		color: var(--uap-font-color-secondary);
	}

	&__name {
		color: var(--uap-font-color);

		font-size: 14px;
		font-weight: 600;

		display: inline-flex;
		flex-wrap: wrap;
		align-items: center;
		
		white-space: pre-wrap;
	}

	&--invalid {
		background: #f9e2e2;

		uo-icon {
			color: #c00;
			border-color: #c00;
		}
	}

	&--cm-focused {
		box-shadow: 0 0 0 2px #0790e8;
	}

	:hover {}

	/* Exceptions for the tokens inside the token selector */

	.form-variable-group-source__description & {
		padding: 2px 6px;

		&__name {
			font-size: 12px;
		}
	}

	// Token inside a token
	#{ $token } {
		border: 1px solid #bbb;
		background: #fff;
		padding: 2px 5px;
	}
}
