@import "@builder-css/mixins.scss";

/*
* Update title
*/

#titlewrap {
	position: relative;
	font-size: var(--uap-font-size--body-large);

	$padding-sides: 12px;

	#title-prompt-text,
	#title {
		font-size: var(--uap-font-size--body-large);
		font-family: var(--uap-font-family--header);
		border-radius: var(--uap-border-radius);

		height: 38px;
		padding-left: $padding-sides;
		padding-right: $padding-sides;

		font-weight: 600;
	}

	&::before {
		@include icon;
		content: '';

		position: absolute;
		top: 50%;
		right: $padding-sides;
		transform: translateY(-50%);
	}

	&.uap-title {

		// Success
		&--success {
			&::before {
				content: var(--uap-icon-check);
				color: var(--uap-color-green);
			}
		}

		// Loading
		&--loading {
			&::before {
				@include icon-loading;

				color: var(--uap-font-color);

				left: auto;
				// Besides adding the padding we're using for the field, remove the space added by the transform
				// in the animation
				right: calc( $padding-sides + calc( -1 * calc( var(--uap-font-size--body-large) / 2 ) ) );
			}
		}
	}
}