@import "./fields-select-select2.scss";

.uap .form-element__select {
	width: 100%;
	padding: 6px 12px;

	border-radius: 8px;
	border: 1px solid #cdcdcd;
	transition: all 150ms ease-in-out;

	outline: none !important;

	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
}

.uap .form-element--loading .form-element__select {
	background: #ebebeb !important;
	border-color: #cdcdcd !important;
	cursor: not-allowed;
	pointer-events: none; /* Prevent clicks */
}

.uap .form-element--error .form-element__select {
	border-color: #e85113 !important;
}

.uap .form-element__select-normal-options {}

/* Select2 */

.uap-select2-item__meta {
	opacity: 0.6;
}
