@import "./mixins.scss";

:root {
	--uap-builder-metabox-padding: 15px;
}

.uap {

	/* Use this class as main container to avoid conflicts */
	font-size: 15px;
	font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.uap * {
	box-sizing: border-box;
}

.uap .uap-clear::after {
	content: "";
	display: block;
	clear: both;
}

/**
 * Increase the size of the Publish metabox
 */

#post-body.columns-2 #postbox-container-1,
#poststuff #post-body.columns-2 #side-sortables {
	min-width: 282px;
}

/**
 * Main container
 */

.post-type-uo-recipe #uap_items-sortables {
	margin-top: 20px;
	margin-bottom: -20px;
}

/*
* WordPress Reset
*/

.uap input,
.uap select,
.uap textarea {
	margin: 0;
	height: auto;
	line-height: inherit;
	font-size: inherit;
	font-family: inherit;
}

.uap a:focus {
	box-shadow: none;
}

/*
* Tools
*/

.uo-realsize {
	position: absolute !important;
	z-index: -10000 !important;
	visibility: hidden !important;
	opacity: 0 !important;
	left: -200vw !important;
	top: -200vh !important;
}

.uap .uo-toggle {
	display: none !important;
}

.uap .uo-toggle--show {
	display: block !important;
}

/*
* Global
*/

.uap a {
	color: #0790e8;
	text-decoration: none;
}

.uap a,
.uap area,
.uap button,
.uap input,
.uap label,
.uap select,
.uap summary,
.uap textarea {
	-ms-touch-action: manipulation;
	touch-action: manipulation;
}

.uap .hidden {
	display: none;
}

/*
* Animations
*/

@keyframes rotating {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes rotating-centered {
	from {
		transform: translate(-50%, -50%) rotate(0deg);
	}
	to {
		transform: translate(-50%, -50%) rotate(360deg);
	}
}

@keyframes rotating-centered-vertically {
	from {
		transform: translateY(-50%) rotate(0deg);
	}
	to {
		transform: translateY(-50%) rotate(360deg);
	}
}

/*
* Report a bug
*/

.uap-report-bug {
	position: relative;
	z-index: 100000;

	user-select: none;
	font-size: 15px;
	font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.uap-report-bug * {
	box-sizing: border-box;
}

.uap-report-bug__overlay {
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0.5);
	z-index: 100001;
	top: 0;
	left: 0;
}

.uap-report-bug-circle {
	width: 800px;
	height: 800px;
	border-radius: 50%;

	position: absolute;
	right: 0;
	bottom: -25px;
	transform: translate(50%, 50%);

	background: #0790e8;
}

.uap-report-bug-circle__container {
	width: 50%;
	height: 50%;

	position: absolute;
	top: 0;
	left: 0;

	padding: 80px 20px 120px 20px;

	text-align: right;
}

.uap-report-bug-circle__text {
	width: 100%;
	color: #fff;
	font-size: 22px;
	line-height: 1.3;
	padding-left: 40%;
}

.uap-report-bug-circle__dont-show {
	color: #fff;
	font-size: 17px;
	line-height: 1.3;
	padding: 25px 0 0 0;
	text-align: right;
}

.uap-report-bug-circle-checkbox {
	display: block;
	position: relative;
	padding-left: 23px;
	cursor: pointer;
}

.uap-report-bug-circle-checkbox__input {
	display: none !important;
}

.uap-report-bug-circle-checkbox__render {
	box-sizing: border-box;
	height: 16px;
	width: 16px;
	border-radius: 8px;
	transition: all 150ms ease-in-out;
	background: #fff;

	position: relative;
	display: inline-block;
	vertical-align: middle;
}

.uap-report-bug-circle-checkbox:hover .uap-report-bug-circle-checkbox__render {
	border-color: #b9b9b9;
}

.uap-report-bug-circle-checkbox__render::after {
	content: "";
	position: absolute;
	display: block;
	opacity: 0;
	transition: all 150ms ease-in-out;

	left: 5px;
	top: 2px;
	width: 3px;
	height: 7px;
	border: solid #0790e8;
	border-width: 0 3px 3px 0;
	transform: rotate(45deg);
}

.uap-report-bug-circle-checkbox__input:checked ~ .uap-report-bug-circle-checkbox__render::after {
	opacity: 1;
}

.uap-report-bug-circle-checkbox__input:checked ~ .uap-report-bug-circle-checkbox__render {
	border-color: #0790e8;
}

.uap-report-bug-circle__span {
	vertical-align: middle;
	margin-left: 10px;
}

.uap-report-bug-circle__footer {
	width: 100%;
	margin-top: 15px;
	text-align: right;
}

.uap-report-bug-circle__btn {
	color: #fff;
	background: #60b051;
	font-weight: 600;
	font-size: 16px;

	cursor: pointer;

	padding: 6px 20px;
	border-radius: 8px;

	transition: background 150ms ease-in-out;

	display: inline-block;
}

.uap-report-bug-circle__btn:hover {
	background: #54a246;
}

.uap-report-bug__btn {
	position: fixed;
	z-index: 100002;

	bottom: 20px;
	right: 20px;

	display: block;

	background: #fff;

	width: 45px;
	height: 45px;
	border-radius: 50%;

	color: #6c757d;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
	border: 1px solid #b7b7b7;

	transition: border 150ms ease-in-out, box-shadow 150ms ease-in-out, color 150ms ease-in-out;
	outline: none;
}

.uap-report-bug--open .uap-report-bug__btn {
	border: 0;
	box-shadow: none;
}

.uap-report-bug__btn:hover {
	color: #0790e8;
	border-color: #0790e8;
}

.uap-report-bug__btn:active,
.uap-report-bug__btn:focus {
	color: #0790e8;
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}

.uap-report-bug__icon::before {
	@include icons-font;
	content: var( --uap-icon-lightbulb );

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	font-size: 23px;
}

/*
* Modal
*/

.uap .modal {
	$modal: &;

	width: 100vw;
	height: 100vh;

	position: fixed;
	top: 0;
	left: 0;

	display: flex;
	align-items: center;
	justify-content: center;

	overflow-y: visible;

	background: rgba(0, 0, 0, 0.4);

	z-index: 100000; /* something big */
}

html.automator-modal-open body {
	overflow: hidden;
}

.uap .modal__box {
	width: 350px;
	max-width: calc(100% - 40px);
	max-height: calc(100% - 60px);
	overflow: auto;

	/* border: 1px solid #cecece; */
	background: #fff;
	border-radius: 8px;

	padding: 20px;

	box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.2);
}

	.uap .modal--allow-content-overflow .modal__box {
		overflow: visible;
	}

.uap .modal--large .modal__box {
	width: 450px;
}

.uap .modal--extra-large .modal__box {
	width: 650px;
}

.uap .modal--xx-large .modal__box {
	width: 800px;
}

.uap .modal__title {
	font-size: 17px;
	font-weight: 600;
	color: #373737;
	margin-bottom: 15px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.uap .modal__title .modal__title-icon {
	color: #9f6000;

	margin-right: 8px;
}

.uap .modal__warning {
	color: #000;
	background: #fff3cf;
	padding: 10px 15px;
	border-radius: 8px;
	line-height: 1.5;

	margin-bottom: 15px;
}

.uap .modal__warning a {
	color: #000;
	text-decoration: underline;
}

.uap .modal__content {
	font-size: 16px;
	line-height: 1.4;
	margin-bottom: 15px;

	width: auto;
	max-height: 250px;
	overflow-y: auto;

	margin-left: -10px;
	margin-right: -10px;
	padding: 0 10px;
}

	.uap .modal--allow-content-overflow .modal__content {
		overflow: visible;
	}

.uap .modal__content,
.uap .modal__content p {
	font-size: 16px;
	line-height: 1.5;
	margin-top: 0;
}

.uap .modal__content p:last-child {
	margin-bottom: 0;
}

.uap .modal__content p:not(:last-child) {
	margin-bottom: 10px;
}

.uap .modal__content::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(0, 0, 0, 0.09);
}

.uap .modal__content::-webkit-scrollbar-thumb:vertical {
	background: rgba(0, 0, 0, 0.3);
	border-radius: 150px;
	background-clip: padding-box;
	border: 2px solid rgba(0, 0, 0, 0);
	min-height: 10px;
}

.uap .modal__content table {
	width: 100%;

	border-spacing: 0;

	border: 1px solid #e0e0e0;
	border-radius: 8px;
}

.uap .modal__content table th {
	border-bottom: 1px solid #e0e0e0;

	text-align: left;
	color: #373737;
	font-weight: 600;
	font-size: 14px;

	padding: 10px;

	border-left: 1px solid #e0e0e0;
}

.uap .modal__content table td {
	text-align: left;
	color: #373737;
	font-weight: 400;
	font-size: 15px;

	vertical-align: top;

	padding: 10px;

	border-bottom: 1px solid #e0e0e0;
	border-left: 1px solid #e0e0e0;

	word-break: break-all;
}

.uap .modal__content table th:first-child,
.uap .modal__content table td:first-child {
	border-left: 0;
}

.uap .modal__content tbody tr:last-child td {
	border-bottom: 0;
}

.uap .modal__content .modal__content-code pre {
	background: #23241f;
	color: #f8f8f2;

	font-size: 14px;

	overflow-y: auto;

	padding: 15px;
}

.uap .modal__buttons {
	text-align: right;

	padding-top: 15px;
}

.uap .modal__buttons > .uap-btn:not(:last-child) {
	margin-right: 10px;
}

.uap .modal__buttons > .uap-btn:last-child {
	margin-right: 0;
}

/*
* Metabox
*/

.uap .metabox {
	width: 100%;
	background: #fff;
	border: 1px solid #d9d9d9;
	border-radius: 8px;

	margin-bottom: 20px;
}

.uap .metabox__title {
	padding: 10px 15px;
	font-weight: 600;
	border-bottom: 1px solid #eee;
}

.uap .metabox__content {
	padding: 0 15px;
}

.uap .metabox__notice {
	color: #000;
	background: #fdf8d9;
	padding: 10px 15px;
	border-radius: 8px;
	line-height: 1.5;
}

.uap .metabox__notice--info {
	background: #bde5f8;
	color: #00529b;
}

.uap .metabox__notice--error {
	background: #f9e2e2;
}

.uap .metabox__notice--success {
	background: #ecf7ed;
}

.uap .metabox__notice p {
	font-family: inherit;
	font-size: inherit;
	margin: 0;
	padding: 0;
}

.uap .metabox__footer {
	padding-top: 15px;
	padding-bottom: 15px;
}

.uap #recipe-actions-ui.metabox__content > div,
.uap #recipe-triggers-ui > div {
	border-bottom: 1px solid #e9e9e9;
}

.uap #recipe-actions-ui.metabox__content > div:last-child,
.uap #recipe-triggers-ui > div:last-child,
.uap-placeholder > div:last-child {
	border-bottom: none;
	padding-bottom: 0;
}

.uap #recipe-actions-ui.metabox__content > div:last-child:before,
.uap #recipe-triggers-ui > div:last-child:before,
.uap-placeholder > div:last-child:before {
	border-bottom: none;
}

.uap .metabox__closures {
	padding-top: 15px;
	padding-bottom: 15px;
}

.uap .metabox__footer a {}

.uap .metabox__footer a uo-icon {
	font-size: 11px;
}

.uap .metabox__footer .uo-toggle {
	margin-bottom: -10px;
}

/* Actions exception */

.post-type-uo-recipe .postbox {
	border-radius: 8px !important;
	box-shadow: none !important;
	border: 0 !important;

	.postbox-header {
		border: 0;
	}

	.handle-order-higher,
	.handle-order-lower {
		display: none;
	}
}

#uo-recipe-actions-meta-box-ui.postbox,
#uo-recipe-triggers-meta-box-ui.postbox {
	padding-bottom: 15px;
}

#uo-recipe-actions-meta-box-ui,
#uo-recipe-triggers-meta-box-ui,
#uo-automator-publish {
	.inside {
		display: block !important;
	}

	.toggle-indicator {
		display: none !important;
	}
}

#uo-recipe-triggers-meta-box-ui .inside,
#uo-recipe-actions-meta-box-ui .inside {
	margin: 0 !important;
	padding: 0 !important;
	overflow: visible !important;
}

#uo-recipe-triggers-meta-box-ui h2.ui-sortable-handle,
#uo-recipe-actions-meta-box-ui h2.ui-sortable-handle,
#uo-automator-publish h2.ui-sortable-handle {
	border-bottom-color: #e9e9e9;
}

/*
* Notices
*/

.uap .uo-notice {
	color: #000;
	font-size: 14px;
	line-height: 1.4;

	padding: 10px 15px;

	background: #fff;
	border-radius: 0;
}

.uap .uo-notice--error {
	background: #f9e2e2;
}

.uap .uo-notice--warning {
	background: #fdf8d9;
}

.uap .uo-notice--success {
	background: #ecf7ed;
}

