		margin-top: 10px;
	}

	.uap .uo-notice:last-child {
		margin-bottom: 20px;
	} */

/*
* Publish
*/

#submitdiv {

	/* display: none; */
}

#uo-automator-publish .inside {
	margin: 0;
	padding: 0;
}

.uap #uap-publish-metabox {
	padding: 10px 15px 10px 15px;

	#uo-uap-notices {
		width: auto;
		margin-left: -15px;
		margin-right: -15px;
		margin-bottom: 5px;
		margin-top: -5px;
	}
}

.uap #uap-publish-metabox .metabox__content,
.uap #uap-publish-metabox .metabox__footer {
	font-size: 14px;
}

.uap #uap-publish-metabox .metabox__content {
	padding: 0 0 10px 0;
}

.uap .publish-row {
	width: 100%;
}

.uap .publish-row__visible {
	padding: 5px 0;
}

.uap .publish-row__visible span {
	vertical-align: middle;
	user-select: none;
}

.uap .publish-row__visible span:not(:last-child) {
	margin-right: 3px;
}

.uap .publish-row__icon {
	color: #82878c;
	font-size: 15px;
	width: 18px;
	display: inline-block;
}

.uap .publish-row uo-icon[id=utensils] {
	margin-left: -1px;
}

.uap .publish-row uo-icon[id=calendar] {
	font-size: 17px;
}

.uap .publish-row__name {}

.uap .publish-row__value {
	font-weight: 600;
}

.uap .publish-row__edit {
	color: #0790e8;
	font-weight: 500;
	border-bottom: 1px solid transparent;
	padding-bottom: 1px;
	margin-left: 1px;
	cursor: pointer;
