	background: #fff;
	border: 1px solid #d9d9d9;
	border-radius: 8px;

	padding: 15px;
	margin-top: 5px;
	margin-bottom: 5px;
}

.uap .publish-row__settings .uap-btn:not(:last-child) {
	margin-right: 10px;
}

.uap #uap-publish-metabox .metabox__footer {

	/* background: #f5f5f5; */
	border-top: 1px solid #e9e9e9;
	margin-top: 0;
	padding-bottom: 5px;
}

.uap .publish-footer__left {
