}

.uap #uap-publish-metabox .publish-footer__move-to-draft {
	color: #a00a00;
	border-bottom: 1px solid transparent;
	padding-bottom: 1px;
	transition: border 150ms ease-in-out;
}

.uap #uap-publish-metabox .publish-footer__move-to-draft:hover {
	border-bottom-color: #a00a00;
}

.uap .publish-footer__right {
	float: right;
}

.uap #uap-publish-metabox .form-checkbox__container {
	margin-bottom: 0;
}

/*
* Buttons
*/

.uap .uap-btn {
	outline: none;
	display: inline-block;
	font-weight: 500;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	user-select: none;
	border: 1px solid transparent;
	padding: 5px 15px;
	font-size: 14px;
	line-height: 1.5;
	border-radius: 8px;
	background-color: transparent;
	transition: color 150ms ease-in-out, background-color 150ms ease-in-out, border-color 150ms ease-in-out, box-shadow 150ms ease-in-out;
	cursor: pointer;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
	position: relative;
}

	.uap-btn uo-icon {
		color: #0790e8;
		margin-right: 5px;
		transition: color 150ms ease-in-out;
	}

.uap .uap-btn--primary {
	color: #0790e8;
	border-color: #0790e8;
}

.uap .uap-btn--primary.uap-btn--loading,
.uap .uap-btn--primary.uap-btn--disabled,
.uap .uap-btn--primary:disabled,
.uap .uap-btn--primary:hover,
.uap .uap-btn--primary:focus {
	color: #fff;
	background-color: #0790e8;

	uo-icon {
		color: #fff;
	}
}

.uap .uap-btn--primary:not(.uap-btn--disabled):active,
.uap .uap-btn--primary:not(.uap-btn--disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}

.uap .uap-btn--secondary {
	color: #6c757d;
	border-color: #b7b7b7;
}

.uap .uap-btn--secondary.uap-btn--loading,
.uap .uap-btn--secondary.uap-btn--disabled,
.uap .uap-btn--secondary:disabled,
.uap .uap-btn--secondary:hover,
.uap .uap-btn--secondary:focus {
	color: #fff;
	border-color: #9e9e9e;
	background-color: #9e9e9e;

	uo-icon {
		color: #fff;
	}
}

.uap .uap-btn--secondary:not(.uap-btn--disabled):active,
.uap .uap-btn--secondary:not(.uap-btn--disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.2);
}

.uap .uap-btn--red {
	color: #f16666;
	border-color: #ff9494;
}

.uap .uap-btn--red.uap-btn--loading,
.uap .uap-btn--red.uap-btn--disabled,
.uap .uap-btn--red:disabled,
.uap .uap-btn--red:hover,
.uap .uap-btn--red:focus {
	color: #fff;
	border-color: #f16666;
	background-color: #f16666;
}

.uap .uap-btn--red:not(.uap-btn--disabled):active,
.uap .uap-btn--red:not(.uap-btn--disabled):focus {
	box-shadow: 0 0 0 0.2rem rgba(241, 102, 102, 0.2);
}

.uap .uap-btn--transparent {
	color: #6c757d;
	border-color: transparent;
	box-shadow: none;
	padding-left: 5px;
	padding-right: 5px;
}

.uap .uap-btn--transparent.uap-btn--loading,
.uap .uap-btn--transparent.uap-btn--disabled,
.uap .uap-btn--transparent:disabled,
.uap .uap-btn--transparent:hover,
.uap .uap-btn--transparent:focus {
	color: #575d63;
	border-color: transparent;
	background-color: transparent;
}

.uap .uap-btn--disabled,
.uap .uap-btn:disabled {
	opacity: 0.6;
	cursor: not-allowed;
}

.uap .uap-btn--loading {
	color: transparent !important;
}

.uap .uap-btn--loading::before {
	@include icons-font;
	content: var( --uap-icon-loading );

	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	color: #fff;
	font-size: 16px;

	animation: rotating-centered 2s linear infinite;
}

.uap .uap-btn--loading:hover {
	color: transparent !important;
}

.uap .uap-btn--small {
	padding: 2px 7px;
}

.uap .uap-btn--xsmall {
	padding: 1px 6px;
	font-size: 13px;
}

.uap .uap-btn--xsmall[uap-tooltip]:after {
	white-space: break-spaces;
	min-width: 120px;
}

/*
* Description
* Used to clarify buttons and fields
*/

.uap .description {
	color: #747474;
	font-size: 14px;
}

.uap * + .description {
	margin-top: 7px;
}

/*
* Item
*/

.uap .item {
	width: 100%;
	position: relative;
	min-height: 60px; /* Space to show the icon, 45px icon */
	margin-top: 5px;
	padding-bottom: 15px;
	padding-top: 10px;
}

.uap .item--error {
	/* VALIDATION ERROR */
}

.uap .item--margintop {
	margin-top: 15px;
}

.uap .item-content__description {
	font-size: 15px;
	color: #747474;

	margin-top: 3px;
}

.uap .item-content__description uo-icon {
	font-size: 12px;
}

/* Item actions */

.uap .item-actions {
	user-select: none;
	position: relative;

	order: 3;

	padding-left: 10px;
}

.uap .item-actions__btn {
	color: #b2b2b2;
	font-size: 18px;
	line-height: 1;

	cursor: pointer;

	transition: color 150ms ease-in-out;
}

.uap .item-actions--open .item-actions__btn,
.uap .item-actions__btn:hover {
	color: #6b6b6b;
}

.uap .item-actions__dropdown {
	position: absolute;
	top: calc( 100% + 8px );
	right: -8px;
	display: none;
	z-index: 10;
}

.uap .item-actions--open .item-actions__dropdown {
	display: block;
}

.uap .item-actions-dropdown__ul {
	position: absolute;
	top: 0;
	right: 0;

	display: block;

	margin: 0; /* Reset browser margin */
	padding: 0; /* Reset browser padding */

	list-style: none;

	background: #fff;

	border: 1px solid #c7c7c7;
	border-radius: 8px;

	box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
}

.uap .item-actions-dropdown__ul::before,
.uap .item-actions-dropdown__ul::after {
	content: "";

	width: 0;
	height: 0;

	position: absolute;
}

.uap .item-actions-dropdown__ul::before {
	z-index: 2;

	top: -8px;
	right: 9px;

	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-bottom: 8px solid #fff;
}

.uap .item-actions-dropdown__ul::after {
	z-index: 1;

	top: -9px;
	right: 7px;

	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	border-bottom: 9px solid #c7c7c7;
}

.uap .item-actions-dropdown__action {
	padding: 0;	/* Reset WP padding */
	margin: 0;	/* Reset WP margin */
	position: relative;
}

.uap .item-actions-dropdown__action:not(:last-child) {
	border-bottom: 1px solid #c9c9c9;
}

.uap .item-actions-dropdown__action:hover {}

.uap .item-actions-dropdown__action a,
.uap .item-actions-dropdown__action span {
	color: #3c3c3c;

	max-width: 100%;
	min-width: 100px;

	cursor: pointer;
	display: block;

	padding: 7px 10px;

	transition: color 150ms ease-in-out;
}

.uap .item-actions-dropdown__action a:hover,
.uap .item-actions-dropdown__action span:hover {
	color: #0790e8;
}

.uap .item-actions-dropdown__action::after {
	content: "";
	opacity: 0;
	transition: opacity 150ms ease-in-out;
}

/* Loading */

.uap .item-actions-dropdown__action--loading {
	pointer-events: none;
	cursor: default;
}

.uap .item-actions-dropdown__action--loading a,
.uap .item-actions-dropdown__action--loading span {
	color: #8e8e8e;
}

.uap .item-actions-dropdown__action--loading::after {
	@include icons-font;
	content: var( --uap-icon-loading );

	opacity: 1;

	position: absolute;
	top: 50%;
	right: 3px;
	z-index: 1;

	color: #8e8e8e;
	font-size: 15px;

	animation: rotating-centered 2s linear infinite;
}

/* Item title */

.uap .item-title__describe-container {
	width: 100%;
	display: flex;
	margin-bottom: -10px;
}

.uap .item-title__describe {
	flex: 1 1 auto;

	color: #343434;
	font-size: 16px;

	margin-bottom: 5px;
}

.uap .item--missing .item-title__describe {
	opacity: 0.6;
}

.uap .item-title__normal,
.uap .item-title__token {
	display: inline-block;
	vertical-align: middle;

	margin-bottom: 5px;
	margin-right: 8px;

	transition: all 150ms ease-in-out;
}

.uap .item:not(.item--closure) .item-title__normal {

	/*
					border-bottom: 1px dashed #999999;
					padding-bottom: 2px;
					cursor: pointer;
					*/
}

.uap .item:not(.item--closure) .item-title__normal:hover {

	/*
						color: #0790e8;
						border-color: #0790e8;
						*/
}

.uap .item-title__token {
	border: 1px solid #0790e8;
	color: #0790e8;

	font-size: 14px;
	padding: 3px 8px;
	border-radius: 8px;

	font-weight: 500;

	cursor: pointer;

	/* white-space: nowrap; */

	display: inline-block;
	vertical-align: middle;

	transition: background 150ms ease-in-out, color 150ms ease-in-out, border 150ms ease-in-out, box-shadow 150ms ease-in-out;
}

.uap .item-title__token[uap-tooltip]::after {
	line-height: 1.4;

	white-space: normal;
	overflow: visible;
	text-overflow: clip;
}

.uap .item-title__token:hover {
	border-color: #0373ba;
}

.uap .item-title__token--filled {
	background: #0790e8;
	color: #fff;
}

.uap .item-title__token--filled:hover {
	background: #0373ba;
}

.uap .item-title__token.item-title__token--open {
	border-color: #0373ba;
}

.uap .item-title__token--filled.item-title__token--open {
	background: #0373ba;
}

.uap .item-title__token--open:not(.item-title__token--filled) {
	color: #0373ba;
}

.uap .item-title__token--open:hover {}

.uap .item-title__token--error {

	/* VALIDATION ERROR */
	color: #fff !important;
	background: #e85113 !important;
	border-color: #e85113 !important;
}

.uap .item-title__token--error-shake {
	animation: shake__variable 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.uap .item-title__token--error:hover {
	background: #be4a46 !important;
	border-color: #be4a46 !important;
}

@keyframes shake__variable {
	10%, 90% {
		transform: translate3d(-1px, 0, 0);
	}

	20%, 80% {
		transform: translate3d(2px, 0, 0);
	}

	30%, 50%, 70% {
		transform: translate3d(-4px, 0, 0);
	}

	40%, 60% {
		transform: translate3d(4px, 0, 0);
	}
}

/* Missing item */

.uap .item--missing .item-title__token,
.uap .item--missing .item-title__token:hover {
	color: #343434;
	border-color: #a2a2a2;
	cursor: default;
}

.uap .item--missing .item-title__token--filled,
.uap .item--missing .item-title__token--filled:hover {
	color: #fff;
	border-color: #929292;
	background: #929292;
}

.uap .item-status {
	flex: 0 0 auto;
}

.uap .item--missing .form-checkbox {
	pointer-events: none;
	cursor: default;
	opacity: 0.6;
}

/* Exception */

.uap .item-title .uap-btn {
	margin-top: 10px;
}

.uap .item-title .uap-btn--secondary {
	color: #a3a3a3;
	border-color: #cdcdcd;
}

.uap .item-title .uap-btn--secondary:hover,
.uap .item-title .uap-btn--secondary:focus {
	color: #fff;
	background: #adadad;
	border-color: #adadad;
}

/* Item integrations */

.uap .item-integrations {
	width: 100%;

	display: grid;
	// grid-template-columns: repeat(auto-fill, minmax(145px, 1fr));
	grid-template-columns: repeat(auto-fill, 145px);
	grid-gap: 15px;

	margin-bottom: 15px;
}

/* Item integration */

.uap .item-integration {

	/* width: 145px; */

	/* float: left; */
	cursor: pointer;

	transition: all 150ms ease-in-out;

	border: 1px solid #d3d3d3;
	border-radius: 8px;

	padding-bottom: 10px;

	/* margin-bottom: 10px; */

	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);

	user-select: none;

	position: relative;
}

.uap .item-integration:hover,
.uap .item-integration.item-integration--selected {
	border-color: #0790e8;
}

/* .uap .item-integration:active {
			box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
		} */

/* .uap .item-integration:not(:last-child){
			margin-right: 10px;
		} */

.uap .item-integration--unavailable {
	background: #f3f3f3;
}

.uap .item-integration--unavailable:hover {
	border-color: #b7b7b7;
}

.uap .item-integration--unavailable .item-integration__logo {
	opacity: 0.6;
	filter: saturate(0);
}

.uap .item-integration__logo {
	height: 60px;

	display: flex;
	align-items: center;
	justify-content: center;

	position: relative;
	margin-top: 3px;
}

.uap .item-integration__logo img {
	width: 40px;
	height: 40px;

	display: block;
}

.uap .item-integration__name {
	padding: 0 10px;

	font-size: 13px;
	font-weight: 600;

	color: #6c6c6c;
	text-align: center;

	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.uap .item-integration__badge {
	position: absolute;
	top: 10px;
	right: 10px;
	z-index: 10;

	font-size: 13px;
	font-weight: 600;
	line-height: 1;

	color: #fff;
	background: #6bc45a;

	border-radius: 150px;

	padding: 3px 6px;

	&--warning {
		background: #767676;
	}
}

.uap .item-integration--available .item-integration__badge {
	background: #a4a4a4;
}

/* Item options */

.uap .item-options {
	width: 100%;
	background: #fff;
	border: 1px solid #cdcdcd;
	border-radius: 8px;
	padding: 20px;
	transition: border 150ms ease-in-out;
}

.uap .item-options--nopadding {
	padding: 0;
}

.uap .item-options--noborder {
	border: 0;
}

.uap .item-options--error {

	/* VALIDATION ERROR */
}

/* Item options - Notice */

.uap .item-options__notice {
	color: #383d41;
	background: #e2e3e5;

	padding: 10px 15px;
	border-radius: 8px;
	line-height: 1.5;

	margin-bottom: 15px;
}

.uap .item-options__notice a {
	color: #2c3033;
	border-bottom: 1px solid rgba(44, 48, 51, 0.6);
}

.uap .item-options__notice--info {
	background: #bde5f8;
	color: #00529b;
}

.uap .item-options__notice--info a {
	color: #003d73;
	border-bottom-color: rgba(0, 61, 115, 0.6);
}

.uap .item-options__notice--warning {
	color: #000;
	background: #fdf8d9;
}

.uap .item-options__notice--warning a {
	color: #000;
	font-weight: 500;
	border-bottom-color: rgba(0, 0, 0, 0.6);
}

.uap .item-options__notice--error {
	background: #f9e2e2;
	color: #000;
}

.uap .item-options__notice--error a {
	color: #000;
	font-weight: 500;
	border-bottom-color: rgba(0, 0, 0, 0.6);
}

.uap .item-options__notice--success {
	background: #ecf7ed;
	color: #000;
}

.uap .item-options__notice--success a {
	color: #000;
	font-weight: 500;
	border-bottom-color: rgba(0, 0, 0, 0.6);
}

.uap .metabox__notice p {
	font-family: inherit;
	font-size: inherit;
	margin: 0;
	padding: 0;
}

/* Buttons */

.uap .item-options__buttons {
	width: 100%;
	display: flex;
}

.uap .item-options__buttons .item-options__uo-buttons {}

.uap .item-options__buttons .item-options__developer-buttons {
	text-align: right;
	flex-grow: 1;
}

.uap .item-options__buttons .uap-btn:not(:last-child) {
	margin-right: 10px;
}

/* Item Task selector */

.uap .item__task-selector {
	width: 100%;
	border-radius: 8px;
}

.uap .item__task-selector--loading {

	/* loading class */
}

.uap .item-task-selector__search {
	width: 100%;
}

.uap .item-task-selector__search-input {
	width: 100%;
	padding: 10px 15px;
	color: #444;

	outline: none !important;

	line-height: 1;

	border: 1px solid #cdcdcd;

	border-radius: 8px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;

	transition: all 150ms ease-in-out;
}

.uap .item-task-selector__search-input:hover {
	border-color: #b9b9b9;
}

.uap .item-task-selector__search-input:active,
.uap .item-task-selector__search-input:focus {
	border-color: #0790e8;
}

.uap .item-task-selector__search-input,
.uap .item-task-selector__search-input:hover,
.uap .item-task-selector__search-input:active,
.uap .item-task-selector__search-input:focus {
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
}

.uap .item-task-selector__options {
	border: 1px solid #cdcdcd;
	border-top: 0;
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;

	max-height: calc(4em + 115px);
	overflow-y: auto;
}

.uap .item-task-selector__options::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(0, 0, 0, 0.09);
	border-radius: 150px;
}

.uap .item-task-selector__options::-webkit-scrollbar-thumb:vertical {
	background: rgba(0, 0, 0, 0.3);
	border-radius: 150px;
	background-clip: padding-box;
	border: 2px solid rgba(0, 0, 0, 0);
	min-height: 10px;
}

.uap .item-task-selector__option {
	padding: 10px 15px;
	color: #343434;
	cursor: pointer;
	position: relative;

	display: flex;
	justify-content: space-between;

	transition: all 150ms ease-in-out;
}

.uap .item-task-selector__option-sentence {
	flex: 1 1 auto;
}

.uap .item-task-selector__option-properties {
	flex: 0 0 auto;
	transition: padding 150ms ease-in-out;
}

.uap .item-task-selector__option--show {}

.uap .item-task-selector__option--hide {
	display: none;
}

.uap .item-task-selector__option--no-results {
	pointer-events: none;
	cursor: default;
}

.uap .item-task-selector__option:hover {
	background: #f3f3f3;
}

.uap .item-task-selector__option:not(:last-child) {
	border-bottom: 1px solid #ececec;
}

.uap .item-task-selector__option-variable {
	color: #0790e8;
	font-weight: 500;
}

.uap .item-task-selector-option__badge {
	display: inline-block;

	font-size: 13px;
	font-weight: 600;
	line-height: 1;

	color: #fff;
	background: #6bc45a;

	border-radius: 150px;

	padding: 3px 6px;

	margin-left: 3px;
}

.uap .item-task-selector__option--requires-user {
	white-space: nowrap;

	padding-left: 15px;

	&.item-task-selector__option--loading .item-task-selector__option-properties {
		padding-right: 30px;
	}

	.item-task-selector-option__requires-user {
		background: #d2eaff;
		border-radius: 4px;
		color: #000;
		display: inline-block;
		font-size: 13px;
		font-weight: 400;
		line-height: 1;
		margin-left: 3px;
		padding: 3px 6px;
	}
}

.item-task-selector-options__group:not(:first-child) {
	border-top: 1px solid #cdcdcd;
}

.item-task-selector-options__group-header {
	background: #f3f3f3;
	color: #565555;
	padding: 10px 15px;
	border-top: 1px solid #cdcdcd;
	border-bottom: 1px solid #cdcdcd;
}

.item-task-selector-options__group-header uo-icon {
	margin-right: 3px;
}

.uap .item-task-selector-options__group--available {}

.uap .item-task-selector-options__group--available .item-task-selector-option__badge {
	background: #a4a4a4;
}

.item-task-selector__option-cannot-add:not(:first-child) {
	border-top: 1px solid #cdcdcd;
}

/* Remove border if it's not the first non available item in the available items */
.item-task-selector__option-cannot-add ~ .item-task-selector__option-cannot-add {
	border-top: 0;
}

.item-task-selector__option-cannot-add .item-task-selector__option-variable {
	color: #000;
}

.item-task-selector-options__group--unavailable {}

.item-task-selector-options__group--unavailable .item-task-selector__option,
.item-task-selector__option-cannot-add {
	background: #f3f3f3;
	outline: 0 !important;
}

.item-task-selector-options__group--unavailable .item-task-selector__option-variable {
	color: #000;
}

.item-task-selector__option uo-icon {
	margin-right: 3px;
}

.item-task-selector-options__group--unavailable:first-child {
	border-top: 0;
}

.uap .item__task-selector--loading .item-task-selector__option:not(.item-task-selector__option--loading) {
	opacity: 0.5;
	cursor: not-allowed;
}

.uap .item-task-selector__option--loading {

	/* loading class */
	background: #f3f3f3;
}

.uap .item-task-selector__option--loading::after {
	@include icons-font;
	content: var( --uap-icon-loading );

	position: absolute;
	right: 15px;

	color: #666;
	font-size: 16px;

	animation: rotating 2s linear infinite;
}

/* Options group */

.uap .item-options__group {
	width: 100%;
	margin-bottom: 15px;
}

.uap .item-options__group:last-child {
	margin-bottom: 20px;
}

.uap .item-options__group--error {

	/* VALIDATION ERROR */
}

/* */

.needs-user-data-modal {
	&-pro-badge {
		font-size: 13px;
		font-weight: 600;
		line-height: 1;

		color: #fff;
		background: #6bc45a;

		border-radius: 150px;

		padding: 3px 6px;

		display: inline-block;

		margin-left: 6px;

		&--gray {
			background: #a4a4a4;
		}
	}

	p {
		margin: 0;
		padding: 0;
	}

	&__title {
		margin-bottom: 15px;
	}

	&__sentence {
		border-radius: 8px;
		line-height: 1.5;
		margin-bottom: 15px;
		padding: 10px 15px;
		border: 1px solid #dfdfdf;
		font-size: 15px;

		span {
			color: #0790e8;
			font-weight: 500;
		}
	}

	&__disclaimer {
		p {
			margin: 0;
			padding: 0;
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		uo-icon[id=external-link-alt] {
			font-size: 12px;
		}
	}
}

.connect-integration-modal {
	&-steps {
		display: table;
		border-bottom: 1px solid #e9e9e9;

		&__list {
			display: table-row-group;
		}

		&__counter {
			color: #747474;
			font-size: 14px;
			padding-bottom: 10px;
		}
	}

	&-step {
		$section_class: &;

		display: table-row;

		&__icon,
		&__description,
		&__action {
			display: table-cell;

			padding: 10px 0;
			border-top: 1px solid #e9e9e9;
		}

		&__icon {
			font-size: 18px;
			padding-right: 10px;

			uo-icon[id=plug] {
				font-size: 19px;
			}

			uo-icon[id=redo] {
				font-size: 16px;
			}
		}

		&__action {
			padding-left: 40px;
			text-align: right;
			vertical-align: middle;

			span {
				display: inline-flex;
				position: relative;
			}

			&-anchor {
				white-space: nowrap;
				display: inline-flex;
				align-items: center;
				position: relative;
				transition: opacity 150ms ease-in-out;
				cursor: pointer;

				uo-icon[id=arrow-circle-right] {
					margin-left: 8px;
				}

				&::after {
					@include icons-font;
					content: var( --uap-icon-loading );

					animation: rotating-centered-vertically 2s linear infinite;
					opacity: 0;
					transition: opacity 150ms ease-in-out;

					top: 50%;
					right: 0;
					position: absolute;
				}

				&--loading {
					pointer-events: none;
					opacity: 0.8;

					&::after {
						opacity: 1;
					}

					uo-icon[id=arrow-circle-right] {
						opacity: 0;
					}
				}
			}
		}

		&--completed {
			#{ $section_class }__icon {
				color: #2e9788;

				&::before {
					@include icon;
					content: var( --uap-icon-check-circle ) !important;
				}

				uo-icon {
					display: none;
				}
			}

			#{ $section_class }__description {
				text-decoration: line-through;
				color: #2e9788;
			}

			#{ $section_class }__action {
				a {
					display: none;
				}
			}
		}

		&--previous-not-completed {
			#{ $section_class }__action {
				a {
					cursor: default;
					opacity: 0.7;
				}
			}
		}

		&-integration-name {
			display: inline-flex;
			align-items: center;
			margin-left: 3px;

			padding-left: calc(1em + 8px);

			position: relative;

			font-weight: 600;

			img {
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);

				width: calc(1em + 2px);
				height: calc(1em + 2px);
			}
		}
	}
}

/**
 * Dynamic sentence
 */


.uap-dynamic-sentence {
	display: inline-flex;
	align-items: center;
	white-space: pre-wrap;
	
	&-pill,
	&-plain {}

	&-pill {
		color: var(--uap-color-blue);
		font-weight: 500;

		&--filled {}
	}
}

