/* Recipe UI changes */
.uap #recipe-actions-ui.metabox__content {
	padding-left: 0;
    padding-right: 0;
}

.uap #recipe-actions-ui .metabox__footer {
    margin-left: 15px;
    margin-right: 15px;
}

.uap #recipe-actions-ui .item {
    padding-left: 15px;
    padding-right: 15px;
    border-bottom-color: white !important;
    background-color: white;
}

.uap #recipe-actions-ui .item::before {
    position: absolute;
    bottom: -1px;
    left: 15px;
    right: 15px;
    display: block;
    height: 1px;
    border-bottom: 1px solid #e9e9e9;
    content: '';
}

.uap .metabox__closures .item::before {
    display: none !important;
}

/* Item reorder */
.uap #recipe-actions-ui .ua-sortable {
    border-bottom: none !important;
}

.uap #recipe-actions-ui .ua-sortable.loading {
    animation: placeHolderShimmer 1s steps(10) 1s infinite alternate;
}

.uap #recipe-actions-ui .ua-sortable .item .item-handle {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 100%;
    top: 0;
    background-color: white;
	border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;
    opacity: 0;
    transition-property: opacity;
    transition-duration: 0.25s;
    cursor: move;
}

.uap #recipe-actions-ui .ua-sortable .item.hover .item-handle {
    opacity: 1;
}

.uap #recipe-actions-ui .ua-sortable .item.sortable-dragging {
    opacity: 0.9;
}

.uap #recipe-actions-ui .ua-sortable .item.sortable-placeholder {
    height: 90px;
    padding: 0;
    margin: 0;
    background: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='4.5' cy='4.5' r='1.5' fill='%23C4C4C4'/%3E%3C/svg%3E");
    border: none;
}

.uap #recipe-actions-ui .ua-sortable .item.sortable-placeholder:before {
    border-bottom-color: white;
}

