.uap-recipe-type {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;
	gap: 0 20px;
	grid-template-areas: "Logged-in Everyone";

	// Hide the inputs
	input {
		display: none;
	}

	&-item {
		// Assign grid areas and icons
		&[data-id="user"] {
			grid-area: Logged-in;

			&::before {
				background-image: url("img/recipe-type-user.svg");
			}
		}

		&[data-id="anonymous"] {
			grid-area: Everyone;

			&::before {
				background-image: url("img/recipe-type-anonymous.svg");
			}
		}

		display: flex;
		flex-direction: column;
		align-items: center;
		border: 1px solid var(--uap-border-color-gray);
		border-radius: var(--uap-border-radius);
		cursor: pointer;
		padding: 20px;
		transition: border 150ms ease-in-out, background 150ms ease-in-out;

		// Icon
		&::before {
			content: "";
			display: block;
			width: 125px;
			height: 125px;
			border: 1px solid var(--uap-border-color-gray);
			border-radius: 50%;
			background-repeat: no-repeat;
			background-size: contain;
			background-position: bottom center;
			background-color: #fff;
		}

		h3 {
			font-size: var(--uap-font-size--body-regular);
			line-height: var(--uap-line-height--body-regular);
			color: var(--uap-font-color);
			width: 100%;
			margin: 20px 0 0;
		}

		p {
			font-size: var(--uap-font-size--body-xsmall);
			line-height: var(--uap-line-height--body-xsmall);
			color: var(--uap-font-color-secondary);
			width: 100%;
			margin: 5px 0 0;
		}

		// Selected style
		&[selected] {
			border-color: var(--uap-color-blue);
			background: rgb(var(--uap-color-blue-rgb) 0.02);
		}
	}
}
