.rtl {

	.uap-item-button--conditions {
		margin-left: 15px;
	}

	.uap .item-title__describe {
		direction: ltr;
		text-align: right;
	}

	.uap .form-checkbox.form-checkbox--toggle .checkbox--show {
		left: 0 !important;
		right: auto;
	}

	.uap .form-checkbox.form-checkbox--toggle .checkbox-label {
		position: absolute;
		left: 35px;
		right: auto;
	}

	.uap .form-checkbox--toggle-right .form-checkbox__container {
		padding-right: 0 !important;
		padding-left: 60px !important;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--show {
		right: 0;
	}

	.uap .form-checkbox__container {
		padding-left: 0;
		padding-right: 23px;
	}

	.uap .item-title__normal, .uap .item-title__token {
		margin-left: 8px;
		margin-right: 0;
	}

	.uap .form-radio__container {
		padding-left: 0;
		padding-right: 23px;
	}

	.uap .radio--show {
		right: 0;
		left: auto;
	}

	.uap .form-element .delay-fields__unit {
		margin-right: 0;
		margin-left: 10px;
	}

	.uap .form-element .delay-fields__unit .select2-selection--single {
		border-bottom-left-radius: 8px;
		border-top-left-radius: 8px;
		border-bottom-right-radius: 0;
		border-top-right-radius: 0;
	}

	.uap .form-element .delay-fields__number .CodeMirror {
		border-bottom-right-radius: 8px;
		border-right: 1px solid #cdcdcd;
		border-top-right-radius: 8px;

		border-bottom-left-radius: 0;
		border-left: 0;
		border-top-left-radius: 0;
	}

	.uap .form-element .schedule-fields__date {
		margin-right: 0;
		margin-left: 15px;
	}

	.uap .modal__buttons > .uap-btn:not(:last-child) {
		margin-right: 0;
		margin-left: 10px;
	}

	.uap .form-variable__element:not(.form-variable__element--empty) .form-variable__toggle {

		border-bottom-right-radius: 0;
		border-left: 1px solid #cdcdcd;
		border-top-right-radius: 0;

		border-bottom-left-radius: 8px;
		border-right: 0;
		border-top-left-radius: 8px;

	}

	.uap .form-element > div:not(.form-textarea) .form-variable__element:not(.form-variable__element--empty) .form-element__input,
	.uap .form-element > div:not(.form-textarea) .form-variable__element:not(.form-variable__element--empty) .form-element__input-cm .CodeMirror {
		border-bottom-left-radius: 0 !important;
		border-top-left-radius: 0 !important;
		border-bottom-right-radius: 8px !important;
		border-top-right-radius: 8px !important;
	}

	.uap .item-options__buttons .uap-btn:not(:last-child) {
		margin-right: 0;
		margin-left: 10px;
	}

	.uap .wp-editor-wrap .wp-switch-editor.switch-html {
		border-top-left-radius: 0;
		border-top-right-radius: 8px;
	}

	.uap .wp-editor-wrap .wp-editor-container {
		border-top-left-radius: 0;
		border-top-right-radius: 8px;
	}
}
