.uap-conditions {
	$condition: &;

	&-options {

		/**
		 * Header
		 */
		&-header {
			border-bottom: 1px solid var(--uap-border-color-gray);

			padding-bottom: 20px;
			margin-bottom: 20px;

			display: flex;
			flex-direction: column;
			gap: 8px;

			&-integration {
				display: flex;
				align-items: center;
				gap: 10px;

				color: var(--uap-font-color-secondary);
				font-size: var(--uap-font-size--body-xsmall);
				line-height: var(--uap-line-height--body-xsmall);
			}

			.uap-dynamic-sentence {
				font-size: var(--uap-font-size--body-regular);

				.uap-dynamic-sentence-pill {
					padding: 3px 8px;

					display: inline-flex;
					align-items: center;
					white-space: pre-wrap;

					color: var(--uap-color-blue);

					border-radius: var(--uap-border-radius);
					border: 1px solid var(--uap-color-blue);

					margin: 0 3px;

					font-size: var(--uap-font-size--body-xsmall);
					line-height: var(--uap-line-height--body-xsmall);

					&--filled {
						background: var(--uap-color-blue);
						color: #fff;
					}
				}
			}
		}

		/**
		 * Fields placeholder
		 */
		&-fields-placeholder {
			line-height: var(--uap-line-height--body-small);
		}
	}

	/**
	 * ================== Exceptions ==================
	 */

	/**
	 * WordPress > A tokens meets a condition
	 */

	&-options {
		&[data-integration="WP"][data-condition="TOKEN_MEETS_CONDITION"],
		&[data-integration="GEN"][data-condition="TOKEN_MEETS_CONDITION"] {
			// Hide the header
			#{ $condition }-options-header {
				display: none;
			}

			// Show all fields in the same row
			#{ $condition }-options-fields {
				display: grid;
				grid-template-columns: repeat( 3, minmax(0, 1fr) );
				gap: 15px;

				// Hide the label of the fields "Criteria" and "Value"
				label[for^="CRITERIA"],
				label[for^="VALUE"] {
					// Visibility instead of display because we want
					// the element to take the space
					visibility: hidden;
				}
			}
		}
	}
}