.uap {
	.form-repeater {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 15px;
		}

		&__label {
			width: 100%;
			display: block;
			margin-bottom: 10px;
			font-weight: 600;
			position: relative;
			transition: color 150ms ease-in-out;

			&-required {
				// Asterisk
				display: inline-block;
				vertical-align: top;
				font-size: 5px;
				color: #ff6060;
				line-height: 1;

				&::before {
					@include icons-font;
					content: var( --uap-icon-asterisk );
				}
			}
		}

		&-fields {
			border: 1px solid #cdcdcd;
			border-radius: 8px;

			&__table {
				width: 100%;

				border-collapse: collapse;

				table-layout: fixed;

				&-heading {
					&-cell {
						border-bottom: 1px solid #cdcdcd;

						text-align: left;
						color: #707070;
						font-weight: 500;
						font-size: 14px;

						padding: 8px;

						&--number {
							width: 30px;

							text-align: center;
						}

						&--actions {
							width: 16px + 16px;

							text-align: center;
						}

						&-title {}

						&-required {
							display: inline-block;
							vertical-align: top;

							font-size: 5px;
							color: #ff6060;

							line-height: 1;

							margin-top: 2px;

							&::before {
								@include icons-font;
								content: var( --uap-icon-asterisk );
							}
						}

						&-description {
							font-weight: 400;
						}
					}
				}

				&-rows {
					tr {
						background: #fff;
						transition: opacity 150ms ease-in-out;
					}

					.sortable-placeholder {
						border: 0;
						border-bottom: 1px solid #e1e1e1;
					}

					.sortable-dragging {
						opacity: 0.5;
						box-shadow: none !important;

						td {
							box-shadow: none !important;
						}
					}

					&-cell {
						text-align: left;
						color: #707070;
						font-weight: 400;
						font-size: 14px;

						vertical-align: top;

						padding: 8px;

						border-bottom: 1px solid #e1e1e1;
						border-left: 1px solid #e1e1e1;

						background: #fff;

						&--number {
							width: 30px;

							text-align: center;

							color: #aaa;
							font-weight: 500;

							border-left: 0;

							cursor: move;

							background: #f4f4f4;

							vertical-align: middle;
						}

						&--actions {
							width: 16px + 16px;

							padding-left: 7px;
							padding-right: 7px;

							text-align: center;

							background: #f4f4f4;

							vertical-align: middle;
						}

						&-remove {
							display: inline-block;

							span {
								width: 16px;
								height: 16px;

								background: #ff7979;

								border-radius: 50%;

								cursor: pointer;

								position: relative;

								display: inline-block;

								&::before {
									@include icons-font;
									content: var( --uap-icon-times );

									font-size: 10px;
									color: #fff;

									position: absolute;
									top: 50%;
									left: 50%;
									transform: translate(-50%, -50%);
								}
							}
						}

						.form-element__error {
							margin-top: 5px;
						}
					}
				}
			}

			&__actions {
				display: flex;

				padding: 8px;
			}
		}

		&__description {
			width: 100%;

			margin-top: 7px;

			color: #747474;
			font-size: 14px;
		}
	}
}
