/* Item schedule */
.uap .item-async {
	display: inline-flex;
	flex-direction: column;
	align-items: center;

	margin-left: 10px;

	position: relative;
}

.item-async {
	opacity: 0;
	transition: opacity 150ms ease-in-out;

	.item--hasSchedule &,
	.uap-item:hover &,
	.uap-item--open & {
		opacity: 1;
	}
}

.uap .async-remove {
	z-index: 999;
	display: none;

	margin-top: 4px;
	font-size: 13px;
	font-weight: 500;
	padding: 2px 8px;

	background-color: #fff;
	color: #e85113;

	border: 1px solid #cdcdcd;
	border-radius: 6px;

	position: absolute;
	top: calc( 100% - 5px );

	vertical-align: middle;

	cursor: pointer;
	white-space: nowrap;

	transition: all 150ms ease-in-out;
}

.uap .item-async:hover .async-remove {
	display: block;
}

.uap .item-async.disabled:hover .async-remove {
	display: none;
}

.uap .async-remove::before {
	content: "";
	width: 0;
	height: 0;
	position: absolute;
	z-index: 2;
	bottom: 100%;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	text-align: center;
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-bottom: 5px solid #fff;
}

.uap .async-remove::after {
	z-index: 1;
	bottom: 100%;
	margin-left: auto;
	margin-right: auto;
	left: 0;
	right: 0;
	text-align: center;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-bottom: 5px solid #c7c7c7;
	content: "";
	width: 0;
	height: 0;
	position: absolute;
}

.uap .async-remove uo-icon {
	margin-right: 4px;
	vertical-align: middle;
	display: inline-block;
}

// Modal
.uap .modal--async-action .modal__box {
	width: 480px;
	overflow-y: visible;
}

.uap .modal--async-action .modal__content {
	min-height: 250px;
	max-height: none;
	margin-top: 20px;
}

.uap .async-action-modal label {
	font-weight: bold;
	margin-bottom: 0 !important;
}

.uap .delay-options, .uap .schedule-options {
	padding-left: 23px;
}

.uap .async-mode__description {
	color: #818181;
	display: inline-block;
	margin-bottom: 10px;
}

.uap .delay-fields {
	margin-bottom: 32px;
	position: relative;
}

.uap .delay-fields__description {
	font-weight: 500;
}

.uap .delay-fields .form-element__error {
	position: absolute;
	bottom: -32px;
}

.uap .delay-fields > div, .uap .schedule-fields > div {
	display: inline-block;
	vertical-align: middle;
	margin-bottom: 0 !important;
}

.uap .form-element .delay-fields__number {
	width: auto;
	min-width: 41px;
}

.uap .form-element .delay-fields__number .CodeMirror {
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;

	border-right: 0;

	min-height: 37px !important;
	padding-top: 6px !important;
	padding-bottom: 6px !important;
}

.uap .form-element .delay-fields__unit {
	width: 110px;
	margin-right: 10px;
}

.uap .form-element .delay-fields__unit .select2-selection--single {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
	min-height: 37px;
	line-height: 14px !important;
	padding-top: 0;
	padding-bottom: 0;
	margin-bottom: 0 !important;
}

.uap .form-element .delay-fields__unit .select2-selection--single {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
	min-height: 37px;
	line-height: 14px !important;
	padding-top: 0;
	padding-bottom: 0;
	margin-bottom: 0 !important;
}

.uap .form-element .schedule-fields__date {
	width: auto;
	min-width: 140px;
	margin-right: 15px;
}

.uap .schedule-fields__date .form-element__input-cm .CodeMirror,
.uap .schedule-fields__time .form-element__input-cm .CodeMirror {
	padding-left: 35px;
}

.uap .schedule-fields__date .form-element__input-cm .CodeMirror::before,
.uap .schedule-fields__time .form-element__input-cm .CodeMirror::before {
	@include icons-font;
	
	position: absolute;
	left: 12px;
	top: 50%;
	transform: translateY(-50%);
}

.uap .schedule-fields__date .form-element__input-cm .CodeMirror::before {
	content: var( --uap-icon-calendar );
}

.uap .schedule-fields__time .form-element__input-cm .CodeMirror::before {
	content: var( --uap-icon-clock );
}

.uap .form-element .schedule-fields__time {
	width: auto;
	min-width: 100px;
}

.uap .schedule-fields__description {
	display: inline-block;
	margin-top: 6px;
	font-size: 80%;
}

.uap .schedule-fields__description uo-icon {
	font-size: 10px;
}

.uap .schedule-notice {
	border-radius: 8px;
	padding: 6px 15px;
	margin-top: 10px;
	font-weight: 500;
}

.uap .schedule-notice uo-icon {
	margin-left: 4px;
	font-size: 12px;
}

.uap .schedule-notice__yellow {
	background-color: #fff3cf;
}

.uap .schedule-fields__description uo-icon {
	margin-left: 4px;
}

.uap .async-notice__blue {
	padding: 6px 12px 8px;
	border-radius: 8px;
	background-color: #e2edf9;
	font-weight: 500;
}
.uap .async-notice__blue uo-icon {
	margin-right: 4px;
	font-size: 12px;
}

// Hide the search field in the dropdown
.select2-search__field.hide-this {
	display: none;
}

/*
* Animations
*/

@keyframes rotating {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
