.uap-conditions {
	$condition: &;

	/**
	 * Button on action
	 */
	&-action-button {
		opacity: 0;

		transition: opacity 150ms ease-in-out;

		// Hidden by default, display when the item is opened
		/* stylelint-disable-next-line selector-class-pattern */
		.uap-item--open &,
		// and when the user hovers on the item
		.uap-item:hover & {
			opacity: 1;
		}
	}
}
