.uap-conditions {
	$condition: &;

	/**
	 * Block
	 */
	&-block {
		$block: &;

		margin: var(--uap-builder-metabox-padding);
		width: auto;

		border: 1px solid var(--uap-border-color-gray);
		border-radius: var(--uap-border-radius);

		box-shadow: var(--uap-box-shadow-small);

		position: relative;

		transition: border-radius 150ms ease-in-out;

		&-logic {
			background: #f9f9f9;

			border-bottom: 1px solid var(--uap-border-color-gray);
			border-radius: var(--uap-border-radius);
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;

			padding: var(--uap-builder-metabox-padding);

			transition: border-radius 150ms ease-in-out;

			// Change border radius on change so the drag handler aligns better
			.ua-sortable #{ $condition }-block:hover & {
				border-top-left-radius: 0;
			}

			&-header {
				display: flex;
				justify-content: space-between;

				&-mode {
					font-weight: 600;

					> span {
						color: #b4b4b4;
					}
				}

				&-dropdown {
					uo-dropdown > uo-icon {
						color: #b2b2b2;
						font-size: 18px;
					}
				}

				// Disable the select if the user can't manage the block
				#{ $block }[disabled] & {
					uo-select {
						pointer-events: none;
					}
				}
			}

			&-content {
				margin-top: 15px;

				display: flex;
				flex-direction: column;

				&-empty {
					color: var(--uap-font-color-secondary);
					font-style: italic;
				}

				.uap-dynamic-sentence span {
					color: var(--uap-font-color);
				}
			}

			&-footer {
				padding-top: 15px;

				&:empty {
					display: none;
				}
			}
		}

		&-warning {
			border-bottom: 1px solid var(--uap-border-color-gray);
		}

		&-actions {
			padding: 5px;

			// Disable actions if the user can't manage the block
			#{ $block }--cannot-manage & {
				filter: grayscale(1);
			}

			// Remove border from the last action
			.uap-item:last-child {
				&::before {
					display: none !important;
				}
			}
		}

		&-drag-handle {
			width: calc(30px + var(--uap-builder-metabox-padding));
			height: 30px;

			padding-right: var(--uap-builder-metabox-padding);

			position: absolute;
			right: 100%;
			top: -1px;
			z-index: 9999; // Bigger than #adminmenuwrap

			background: #f9f9f9;

			border: 1px solid var(--uap-border-color-gray);
			border-right: 0;

			border-top-left-radius: var(--uap-border-radius);
			border-bottom-left-radius: var(--uap-border-radius);
			box-shadow: -7px 5px 5px 0 rgb(0 0 0 / 10%);

			display: flex;
			align-items: center;
			justify-content: center;

			cursor: move;

			opacity: 0;

			transition: opacity 150ms ease-in-out;

			#{ $condition }-block:hover & {
				opacity: 1;
			}
		}

		// Change border radius on change so the drag handler aligns better
		.ua-sortable &:hover {
			border-top-left-radius: 0;
		}
	}
}