.uap-item {
	$item: &;

	--uap-builder-gap-between-rows: 10px;

	&-inner {
		display: grid;
		grid-template-columns: min-content 1fr;
		grid-template-rows: min-content min-content min-content;
		gap: 0 15px;
		grid-template-areas:
			"Sidebar Top-bar"
			"Sidebar Main"
			"Sidebar Inner-content";
	}

	&-sidebar {
		grid-area: Sidebar;
	}

	&-top-header {
		grid-area: Top-bar;

		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: var(--uap-builder-gap-between-rows);

		&-left {
			flex: 1 1 auto;

			display: flex;
			align-items: center;

			font-size: 16px;
		}

		&-right {
			flex: 0 0 auto;

			display: flex;
			align-items: center;
		}
	}

	&-main {
		grid-area: Main;
	}

	&-inner-content {
		grid-area: Inner-content;

		&:empty {
			display: none;
		}
	}

	&-main + &-inner-content {
		margin-top: var(--uap-builder-gap-between-rows);
	}

	/**
	 * Icon
	 */

	&-icon {
		width: 45px;
		height: 45px;

		background: #fff;
		border-radius: 50%;
		border: 1px solid #cdcdcd;

		position: relative;

		box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);

		overflow: hidden;

		img {
			width: 24px;
			height: 24px;

			display: block;

			object-fit: contain;
			object-position: center;

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		&:empty {
			&::before {
				@include icons-font;
				content: var(--uap-icon-bolt);

				color: #c4c4c4;
				font-size: 20px;

				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				transition: opacity 150ms ease-in-out;
			}
		}

		// Style for when the item is missing
		.item-missing & {
			border-style: dashed;
			border-color: #bfbfbf;

			&::before {
				content: var(--uap-icon-exclamation-triangle);
			}
		}
	}

	/**
	 * Tag
	 */
	
	&-tag {
		font-size: 13px;
		font-weight: 600;
		padding: 4px 9px;
		line-height: 1;

		color: #818181;

		border: 1px solid #cdcdcd;
		border-radius: 150px;

		white-space: nowrap;

		display: inline-flex;
	}

	/**
	 * ID
	 */
	
	&-id {
		font-size: var(--uap-font-size--body-xsmall);
		color: var(--uap-font-color-secondary);
		margin-left: 8px;
	}

	/**
	 * Alerts
	 */
	
	&-inner + uo-alert {
		margin-top: 15px;
	}
}
