@mixin icons-font {
	font-family: "Uncanny Automator Icons" !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@keyframes placeHolderShimmer {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0.5;
	}
}

/**
 * icon.scss
 */

@mixin icon {
	font-size: 1em;
	font-family: "Uncanny Automator Icons" !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin icon-loading {
	// Define the name of the keyframes animation
	$keyframes_name: unique_id() + "-rotate";

	// Add icon
	@include icon;
	content: var(--uap-icon-loading);

	// Style it
	color: var(--uap-font-color);

	// Center it
	position: absolute;
	top: 50%;
	left: 50%;

	// Add animation
	animation: #{ $keyframes_name } 2s linear infinite;
	@keyframes #{ $keyframes_name } {
		from {
			transform: translate(-50%, -50%) rotate(0deg);
		}
		to {
			transform: translate(-50%, -50%) rotate(360deg);
		}
	}
}
