#recipe_categorydiv {
	.selectit {
		width: auto;
		position: relative;
		display: block;

		padding-top: 2px;
		padding-bottom: 2px;
		padding-left: 23px;
		padding-right: 25px;

		transition: opacity 150ms ease-in-out;

		// animation: placeHolderShimmer 1s steps(10) 1s infinite alternate;

		line-height: 1.4;

		input {
			position: absolute;
			top: 7px;
			left: 0;

			&:disabled {
				border: 1px solid #7e8993;
			}
		}

		&::before {
			@include icons-font;
			content: var( --uap-icon-loading );

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -6px;
			z-index: 1;

			color: #8e8e8e;
			font-size: 15px;

			animation: rotating-centered 2s linear infinite;

			transition: opacity 150ms ease-in-out;
		}

		&::after {
			@include icons-font;

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -6px;
			z-index: 1;
			transform: translate(-50%, -50%);

			font-size: 15px;

			transition: opacity 150ms ease-in-out;
		}
	}

	.uap-taxonomy-loading {
		pointer-events: none;
		cursor: default;

		opacity: 0.8;

		&::before {
			opacity: 1;
		}
	}

	.uap-taxonomy-success {
		&::after {
			content: var( --uap-icon-check );

			opacity: 1;
			color: #6bc45a;
		}
	}

	.uap-taxonomy-error {
		&::after {
			content: var( --uap-icon-times );

			opacity: 1;
			color: #e85113;
		}
	}
}

#tagsdiv-recipe_tag {
	.ui-sortable-handle span {
		position: relative;

		&::before {
			@include icons-font;
			content: var( --uap-icon-loading );

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -32px;
			z-index: 1;

			color: #8e8e8e;
			font-size: 15px;

			animation: rotating-centered 2s linear infinite;

			transition: opacity 150ms ease-in-out;
		}

		&::after {
			@include icons-font;

			opacity: 1;

			position: absolute;
			top: 10px;
			right: -32px;
			z-index: 1;
			transform: translate(-50%, -50%);

			font-size: 15px;

			transition: opacity 150ms ease-in-out;
		}
	}

	.inside {
		transition: opacity 150ms ease-in-out;
		opacity: 1;
	}

	&.uap-tags-loading {
		.inside {
			pointer-events: none;
			cursor: default;
			opacity: 0.8;
		}

		.ui-sortable-handle span::before {
			opacity: 1;
		}
	}

	&.uap-taxonomy-success {
		.ui-sortable-handle span::after {
			content: var( --uap-icon-check );

			opacity: 1;
			color: #6bc45a;
		}
	}

	&.uap-taxonomy-error {
		.ui-sortable-handle span::after {
			content: var( --uap-icon-times );

			opacity: 1;
			color: #e85113;
		}
	}
}
