.uap {
	.user-selector {
		$section_section: ".user-selector";

		border-bottom: 1px solid #e9e9e9;

		display: none;

		&--show {
			display: block;
		}

		.uap-item-icon {
			&::before {
				display: none;
			}

			&::after {
				content: "";

				width: 100%;
				height: calc(100% + 2px);

				position: absolute;
				left: 0;
				top: 2px;

				// background: url("../img/recipe-type-anonymous.svg") no-repeat center bottom;
				background-size: 95%;
			}
		}

		.uap-item-top-header-right {
			uo-icon {
				color: #b2b2b2;
				cursor: pointer;
				font-size: 18px;
			}
		}

		&__field-group {
			width: 100%;
			display: flex;
			justify-content: space-between;

			&--two-columns {
				.form-element {
					width: calc(50% - 10px);
				}
			}
		}

		&__warning {
			color: #000;
			background: #fdf8d9;

			padding: 10px 15px;
			border-radius: 8px;
			line-height: 1.5;

			margin-top: 5px;
			margin-bottom: 15px;
		}

		&-option {
			.item-integration__logo {
				width: 100%;
				height: 55px;

				margin: 15px 0 10px 0;

				background-size: contain;
				background-repeat: no-repeat;
				background-position: center center;
			}

			&--existing-user {
				.item-integration__logo {
					background-image: url("../img/user-selector-existing-user.svg");
				}
			}

			&--new-user {
				.item-integration__logo {
					background-image: url("../img/user-selector-new-user.svg");
				}
			}
		}

		&-summary {
			&-row {
				display: flex;
				align-items: center;

				padding: 5px 0;

				&__icon {
					display: flex;

					width: 27px;

					font-size: 20px;
					color: #818181;
				}

				&__content {
					font-size: 15px;
				}

				&__token {
					display: inline-block;

					margin: 0 3px;

					padding: 5px 10px;
					border-radius: 8px;

					cursor: pointer;

					font-size: 14px;
					font-weight: 500;
					color: #fff;

					background: #7f8fa6;
				}
			}
		}

		&--summary {
			.item-options {
				padding-top: 17px;
				padding-bottom: 17px;
				border-color: #dcdcdc;
			}
		}
	}
}
